import {meet_add,meet} from '../api/recru.js'
export default {
    head: {
        title: '传统行业触网解决方案分享会',
         script: [
      {
        src:
          "https://res2.wx.qq.com/open/js/jweixin-1.6.0.js"
      }
    ]
    },
    name: 'home',
    data() {
        return {
            name2:'',
            phone2:'',
            trade:"",
            recommend:''
        }
    },
   async mounted() {
        if (process.client) {
            document.title = "传统行业触网解决方案分享会";
        }
        const {data} = await meet()
        wx.config({
                debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                appId: 'wx74a82c2291be2790', // 必填，公众号的唯一标识
                timestamp:data.data.timestamp, // 必填，生成签名的时间戳
                nonceStr:data.data.nonceStr, // 必填，生成签名的随机串
                signature:data.data.signature, // 必填，签名
                jsApiList: ["updateAppMessageShareData", "updateTimelineShareData"] // 必填，需要使用的JS接口列表，根据引入的微信js版本不同，jsApi名字不同，具体请看文档
        });
            wx.ready(function() {
                wx.updateTimelineShareData({     //分享到朋友圈
                    title: '传统行业触网解决方案分享会', // 分享标题
                    link: 'https://www.jiunidu.com/seek', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                    imgUrl: 'https://image.jiunidu.com/web/h5_fx_20210407.png', // 分享图标,用绝对路径
                    success: function() {
                        // 用户点击了分享后执行的回调函数
                    },
                });
                wx.updateAppMessageShareData({    //分享到微信好友
                    title: '传统行业触网解决方案分享会', // 分享标题
                    link: 'https://www.jiunidu.com/seek', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                    imgUrl: 'https://image.jiunidu.com/web/h5_fx_20210407.png', // 分享图标
                    desc:'产业链数字化建设，区域商业生态融合', //分享描述
                    success: function () {
                    // 设置成功
                    }
                })
            })
         
    },
    methods:{
       async apply(id) {
                const res = await meet_add({name:this.name2,phone:this.phone2,trade:this.trade,recommend:this.recommend,type:1})
                if(res.data.code === 1) {
                    this.name2 = this.phone2 = this.trade = this.recommend = ''
                    alert(res.data.msg)
                } else {
                    alert(res.data.msg)
                }
        },
        handleInput(e) {
            this.phone2=e.target.value.replace(/[^\d]/g,'');
        }
    }
}
